<template>
  <div>
    <h3>移 动 广 告 投 送 系 统</h3>
    <el-container>
      <el-header>
        <!-- 头像 -->
        <div class="admin" @click="drawer = true">
          <el-avatar
            size="large"
            icon="el-icon-user"
            style="margin-top: 5px"
            :src="this.admin.avater"
          />
          <span>欢迎您！{{ admin.nickname }}</span>
          <!-- 点击修改个人信息 -->
          <el-button type="text" size="small" @click="drawer = true">
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;点击修改个人信息&nbsp;=>
          </el-button>
        </div>
        <el-button
          class="logout el-icon-switch-button"
          type="danger"
          plain
          @click="logout"
        >
          退出登录</el-button
        >
      </el-header>
      <el-container style="margin: 20px 40px 0 40px">
        <el-aside>
          <Aside></Aside>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
    <el-drawer
      title="个人信息"
      :visible.sync="drawer"
      :with-header="false"
      @blur="this.drawer = false"
    >
      <ChangeInfo></ChangeInfo>
    </el-drawer>
  </div>
</template>

<script>
import ChangeInfo from "../components/Admin/ChangeInfo";
import Aside from "../components/Admin/Aside";
export default {
  name: "Admin",

  components: { ChangeInfo, Aside },
  data() {
    return {
      drawer: this.flag,
      admin: {
        nickname: "admin",
        avater:
          "https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png",
      },
    };
  },

  created() {
    this.getAdmin();
  },

  mounted() {},

  methods: {
    getAdmin() {
      this.admin = this.$store.getters.getUser;
    },
    logout() {
      this.$confirm("确定退出登录吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.$axios
          .get("/logout")
          .then((res) => {
            this.$store.commit("REMOVE_INFO");
            this.$router.replace("/home");
          })
          .then(() => {
            this.$message({
              type: "success",
              message: "退出成功",
            });
          });
      });
    },
  },
};
</script>

<style lang="less" scoped>
.el-header {
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-image: linear-gradient(to top, #62d3ff, #b0bcfb);
  .admin {
    padding: 4px 70px 4px 0;
    margin-left: 40px;
    display: flex;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.01);
    span {
      color: #fff;
      margin-left: 20px;
    }
    .el-button {
      color: #007acc;
      margin-top: 20px;
      margin-left: -110px;
    }
  }

  .logout {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-right: 50px;
    text-indent: 5px;
  }
}

.el-aside {
  min-height: 78vh;
  background-color: #545c64;
  color: #333;
  text-align: center;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  line-height: 160px;
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
h3 {
  height: 70px;
  line-height: 70px;
  font-size: 35px;
  font-weight: 400;
  background-image: linear-gradient(to bottom, #e0dcdc, #b0bcfb);
}
/deep/.el-avatar {
  img {
    width: 100%;
  }
}
</style>