<template>
  <div>
    <el-form ref="form" :model="form" label-width="80px" style="padding: 40px">
      <el-form-item label="">
        <el-upload
          class="avatar-uploader"
          action="http://localhost:8081/upload"
          :data="{
            id: this.form.id,
            username: this.form.username,
          }"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="form.avater" :src="form.avater" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="ID:" v-show="false">
        <el-input v-model="form.id" disabled></el-input>
      </el-form-item>
      <el-form-item label="账号:">
        <el-input v-model="form.username" disabled></el-input>
      </el-form-item>
      <el-form-item label="昵称:">
        <el-input v-model="form.nickname"></el-input>
      </el-form-item>
      <!-- 电话 -->
      <el-form-item label="电话:">
        <el-input v-model="form.phone"></el-input>
      </el-form-item>
      <el-form-item label="邮箱:">
        <el-input v-model="form.email"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">保存</el-button>
        <el-button @click="innerDrawer = true" type="warning"
          >修改密码</el-button
        >
      </el-form-item>
    </el-form>

    <el-drawer :append-to-body="true" :visible.sync="innerDrawer">
      <ChangePwd></ChangePwd>
    </el-drawer>
  </div>
</template>

<script>
import ChangePwd from "../../components/Admin/ChangePwd";
export default {
  name: "Changeinfo",

  components: { ChangePwd },
  data() {
    return {
      innerDrawer: false,
      form: {},
    };
  },

  mounted() {},

  methods: {
    onSubmit() {
      this.$axios.post("/change", this.form).then((res) => {
        this.$message({
          type: "success",
          message: "修改成功",
        });
      });
    },
    handleAvatarSuccess(res, file) {
      this.form.avater = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
  },
  created() {
    this.form = this.$store.getters.getUser;
  },
};
</script>

<style lang="less" scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 30px;
  height: 30px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  border-radius: 75px;
  width: 150px;
  height: 150px;
  display: block;
}
</style>