import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Admin from '../views/Admin.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home.vue'),
  },
  {
    path: '/home',
    component: Home
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue')
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import('../views/Register.vue')
  },
  {
    path: '/admin',
    component: Admin,
    // 需要token
    meta: {
      requiresAuth: true
    },
    children: [
      {
        path: '/',
        name: '/allAD',
        meta: {
          requiresAuth: true,
        },
        component: () => import('../components/Admin/Main/AllAD.vue')
      },
      {
        path: '/allAD',
        meta: {
          requiresAuth: true,
        },
        component: () => import('../components/Admin/Main/AllAD.vue')
      },
      {
        path: '/Checkad',
        name: '/Checkad',
        meta: {
          requiresAuth: true
        },
        component: () => import('../components/Admin/Main/CheckAD.vue')
      },
      {
        path: '/Finance',
        name: '/Finance',
        meta: {
          requiresAuth: true
        },
        component: () => import('../components/Admin/Main/Finance.vue')
      },
      {
        path: '/Operate',
        name: '/Operate',
        meta: {
          requiresAuth: true
        },
        component: () => import('../components/Admin/Main/Operate.vue')
      },
    ]
  },
  {
    path: "/user",
    component: () => import('../views/User.vue'),
    meta: {
      requiresAuth: true
    },
    children: [
      {
        path: '/',
        name: '/myAllAD',
        meta: {
          requiresAuth: true,
        },
        component: () => import('../components/User/Main/MyAllAD.vue')
      },
      {
        path: '/myAllAD',
        name: "/myAllAD",
        meta: {
          requiresAuth: true,
        },
        component: () => import('../components/User/Main/MyAllAD.vue')
      },
      {
        path: "/advertising",
        name: "/advertising",
        meta: {
          requiresAuth: true,
        },
        component: () => import('../components/User/Main/Advertising.vue')
      },
      {
        path: "/dataReport",
        name: "/dataReport",
        meta: {
          requiresAuth: true,
        },
        component: () => import('../components/User/Main/DataReport.vue')
      },
      {
        path: "/operationJournal",
        name: "/operationJournal",
        meta: {
          requiresAuth: true,
        },
        component: () => import('../components/User/Main/OperationJournal.vue')
      },
      {
        path: "/financialCenter",
        name: "/financialCenter",
        meta: {
          requiresAuth: true,
        },
        component: () => import('../components/User/Main/FinancialCenter.vue')
      },
      {
        path: "/personalCenter",
        name: "/personalCenter",
        meta: {
          requiresAuth: true,
        },
        component: () => import('../components/User/Main/PersonalCenter.vue')
      },
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
